import { Injectable } from '@angular/core';
import {Router} from '@angular/router'
import * as io from "socket.io-client";
import { ConnectionGuard } from "../disconnect/connection-guard"

@Injectable()
export class SocketService {
  private host: string = window.location.protocol + "//" + window.location.hostname + ":" + 3000;

  public socket: SocketIOClient.Socket;
  constructor(private router:Router,private connectionguard:ConnectionGuard) {
    console.log("[SocketService] socket service")
    this.socket = io.connect(this.host);
    this.socket.on("connect", () => this.connect())
    this.socket.on("disconnect", () => this.disconnect());
    this.socket.on("reconect", () => this.reconnect());
  }

  private connect(): void {
    console.log("[SocketIO] Connected");
    this.connectionguard.isConnect = true;
    this.router.navigateByUrl('/live');
  }

  private disconnect(): void {
    console.log("[SocketIO] Disconnect");
    this.connectionguard.isConnect = false;
    this.router.navigateByUrl('/disconnected');
  }
  
  private reconnect(): void {
    console.log("[SocketIO] Reconnect");
  }
}



