import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { LiveDataComponentComponent } from '../live-data-component/live-data-component.component'
import { SetPointComponent } from '../set-point/set-point.component'
import {SettingsComponent} from '../settings/settings.component'
import {DisconnectComponent} from '../disconnect/disconnect.component';
import {ConnectionGuard} from '../disconnect/connection-guard'

const routes: Routes= [
	{ path: '', redirectTo: '/live', pathMatch: 'full' },
	{ path: 'live',  component: LiveDataComponentComponent, canActivate: [ConnectionGuard] },
	{ path: 'setting',  component: SettingsComponent, canActivate: [ConnectionGuard]},
  { path: 'disconnected',  component: DisconnectComponent }

]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
  	RouterModule
  ]
})
export class AppRoutingModule { }



