import { Component, OnInit } from '@angular/core';
import {ModeService} from '../mode/mode.service'
import {DataService} from '../share/data.service';
import {SocketService} from '../share/socket.service';

@Component({
  selector: 'app-detecting',
  templateUrl: './detecting.component.html',
  styleUrls: ['./detecting.component.css']
})
export class DetectingComponent implements OnInit {

  public selected:number = 0;
  constructor(private modeservice:ModeService, private dataservice:DataService,private io:SocketService) { }

  ngOnInit() {
  }

  UpdateDetecting(){
    console.log(this.dataservice.detecting);
    this.io.socket.emit('DETECTING', this.dataservice.detecting);
  }

}



