import { Injectable } from '@angular/core';
import { SocketService } from './socket.service'
@Injectable()
export class DataService {
  public sensor: SensorModel;
  public setpoint: SetPointModel;
  public manualRelay:any = [false,false,false,false];
  public mode:boolean = false;
    
  public isTimer:boolean = false;
  public isSetpoint:boolean = false;
  public isDetecting:boolean = false;

  public timerList:any = [];
  
  public detecting:any = [{
        'vpd': {
            'set': 2.0,
            'isuse': false
        },
        'soil': {
            'set': 50,
            'isuse': false
        },
        'working': 10,
        'detecting': 10
    },
    {
        'vpd': {
            'set': 2.0,
            'isuse': false
        },
        'soil': {
            'set': 50,
            'isuse': false
        },
        'working': 10,
        'detecting': 10
    },
    {
        'vpd': {
            'set': 2.0,
            'isuse': false
        },
        'soil': {
            'set': 50,
            'isuse': false
        },
        'working': 10,
        'detecting': 10
    },
    {
        'vpd': {
            'set': 2.0,
            'isuse': false
        },
        'soil': {
            'set': 50,
            'isuse': false
        },
        'working': 10,
        'detecting': 10
    }
]

  constructor(public io: SocketService) {
    this.sensor = new SensorModel();
    this.setpoint = new SetPointModel();

    console.log("[DataService] dataservice init");
    this.io.socket.on("SENSOR_DATA", (data: any) => {
      this.sensor.setValue(data.time, data.vpd, data.soil, data.temp, data.humi, data.light);
     // console.log(this.sensor)
    })

    this.io.socket.on("SET_POINT", (data: any) => {
      console.log('[DataService] Set Point\n ' + JSON.stringify(data));
      this.setpoint.setPoint = data;
    })

    this.io.socket.on("MANUAL", (data:any)=>{
      this.manualRelay = data;
    })
    this.io.socket.on("MODE", (data:any)=>{
      this.mode = (data == "AUTO")?true:false;
      console.log(this.mode);
    })

    this.io.socket.on('SETMODE', (data:any)=>{
      console.log(data);
      if(data == "SETPOINT"){
        this.isSetpoint = true;
      }
      else if(data == 'TIMER'){
        this.isTimer = true;

      }
      else if(data == 'DETECTING'){
        this.isDetecting = true;
      }
    })

    this.io.socket.on('TIMER', (data:any)=>{
      console.log(data);
      this.timerList = data;
    });

    this.io.socket.on('DETECTING', (data:any)=>{
      console.log(data);
      this.detecting = data;
    })
  }
}

class SensorModel {
  public time: string;
  public vpd: number;
  public soil: number;
  public temp: number;
  public humi: number;
  public light: number;

  constructor() {
    this.time = "00:00:00";
    this.vpd = 0.0;
    this.soil = 0.0;
    this.temp = 0.0;
    this.humi = 0.0;
    this.light = 0;
  }

  public setValue(time: string, vpd: number, soil: number, temp: number, humi: number, light: number): void {
    this.time = time;
    this.vpd = vpd;
    this.soil = soil;
    this.temp = temp
    this.humi = humi;
    this.light = light;
  }
}

class SetPointModel {
  public setPoint: any = [
    {
      "ch": 1,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    },
    {
      "ch": 2,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    },
    {
      "ch": 3,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    },
    {
      "ch": 4,
      "vpd": [2200, 2250, false],
      "soil": [40, 60, false],
      "use": true
    }
  ]
}



