import { Component, OnInit } from '@angular/core';
import{ModeService} from './mode.service'
import {SocketService} from '../share/socket.service'
import {DataService} from '../share/data.service'

@Component({
  selector: 'app-mode',
  templateUrl: './mode.component.html',
  styleUrls: ['./mode.component.css']
})
export class ModeComponent implements OnInit {
  constructor(private modeservice:ModeService, private io:SocketService,private dataservice:DataService) {
  }
  ngOnInit() {
  }

  onChange($event):void {
    console.log(this.modeservice.isAuto)
  }
  
 

  private onAuto($event):void{
    var mode = {
      'mode': ($event)?"AUTO":"MANUAL"
    }
    
    this.io.socket.emit('MODE', mode);
    if(!$event){
      this.dataservice.isSetpoint = false;
      this.dataservice.isTimer = false;
      this.dataservice.isDetecting = false;
    }
    else {
      this.dataservice.manualRelay = [false,false,false,false]
    }
  
  }

  
}



