import { Component, OnInit } from '@angular/core';
import {ModeService} from '../mode/mode.service'
import {DataService} from '../share/data.service'
import {SocketService} from '../share/socket.service';

@Component({
  selector: 'app-auto',
  templateUrl: './auto.component.html',
  styleUrls: ['./auto.component.css']
})
export class AutoComponent implements OnInit {
  constructor(private dataservice:DataService,private io:SocketService) {}
  ngOnInit() {}
   private onSetpoint($event):void{
    var setmode = {
      'setmode': 'SETPOINT'
    }
    if($event){
      this.dataservice.isDetecting = false;
      this.dataservice.isSetpoint = true;
      this.dataservice.isTimer = false;
       this.io.socket.emit('SETMODE', setmode)
    }
    else{
      setmode.setmode = "";
       this.io.socket.emit('SETMODE', setmode)
      this.dataservice.isSetpoint = false;
    }
  }

  private onTimer($event):void{
    var setmode = {
      'setmode': 'TIMER'
    }
     
    if($event){
      this.dataservice.isDetecting = false;
      this.dataservice.isSetpoint = false;
      this.dataservice.isTimer = true;
      this.io.socket.emit('SETMODE', setmode)
    }
    else{
      setmode.setmode = "";
       this.io.socket.emit('SETMODE', setmode)
      this.dataservice.isTimer = false;
    }
  }

  private onDetecting($event):void{
    var setmode = {
      'setmode': 'DETECTING'
    }
    if($event){
      this.dataservice.isDetecting = true;
      this.dataservice.isSetpoint = false;
      this.dataservice.isTimer = false;
      this.io.socket.emit('SETMODE', setmode)
    }
    else{
      this.dataservice.isDetecting = false;
      setmode.setmode = "";
      this.io.socket.emit('SETMODE', setmode)
    }
  }
}


